@font-face {
  font-family: "Digital";
  src: url("//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.woff2")
      format("woff2"),
    url("//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.svg#Digital-7")
      format("svg");
}
* {
  box-sizing: border-box;
}
body {
  background-color: black;
  color: white;
  font-family: Acme, sans-serif;
}
#time-left {
  font-family: "Digital", sans-serif;
  font-weight: bold;
  font-size: 100px;
  width: 225px;
  color: #ffd700;
  margin: 0 auto;
}
#title,
#timer-label {
  text-align: center;
  font-size: 35px;
}
#timer-label {
  font-size: 30px;
}

.row::after {
  content: "";
  clear: both;
  display: table;
}

.col-1 {
  width: 8.33%;
}
.col-2 {
  width: 16.66%;
}
.col-3 {
  width: 25%;
}
.col-4 {
  width: 33.33%;
}
.col-5 {
  width: 41.66%;
}
.col-6 {
  width: 50%;
}
.col-7 {
  width: 58.33%;
}
.col-8 {
  width: 66.66%;
}
.col-9 {
  width: 75%;
}
.col-10 {
  width: 83.33%;
}
.col-11 {
  width: 91.66%;
}
.col-12 {
  width: 100%;
}

[class*="col-"] {
  float: left;
  padding: 15px;
}
#break-label,
#session-label,
#break-length,
#session-length {
  text-align: center;
  font-size: 25px;
}
#break-decrement,
#break-increment {
  display: inline;
  padding: 20px;
  font-size: 20px;
}
#session-decrement,
#session-increment {
  display: inline;
  padding: 20px;
  font-size: 20px;
}
.break-button {
  text-align: center;
}
.session-button {
  text-align: center;
}

#start-stop,
#reset {
  display: inline;
  padding: 15px;
}
.start-reset-btn {
  text-align: center;
  font-size: 20px;
}
